.width-container {
  width: 100%;
  pointer-events: none;
}

.hidden-container {
  width: 0;
  height: 0;
  overflow: hidden;
  position: relative;
  pointer-events: none;
}

.hidden-inner {
  position: absolute;
}

.menu-list :global .ant-dropdown-menu-item {
  padding: 0 !important;
  background: transparent !important;
}
.menu-list :global .button-group-item {
  padding: 5px 8px !important;
  border: none;
  width: 100%;
}
.menu-list :global .button-group-item:not(:disabled) {
  color: var(--primary-color);
}
.menu-list :global .button-group-item:disabled {
  background: #ffffff;
}
.menu-list :global .button-group-item .ant-btn-primary {
  color: #ffffff;
}
.menu-list :global .button-group-item .ant-btn-loading-icon {
  display: none;
}
.menu-list :global .button-group-item:hover:not(:disabled) {
  color: var(--primary-color);
  background: var(--primary-color-1) !important;
}